import React, { useState } from 'react';
import Layout from '../components/Layout.js';
import { Link } from "gatsby";
import SEO from '../components/seo';
import { StaticImage } from "gatsby-plugin-image"

const capacidadesEcatch = [
  {
    title: "Formularios",
    description: "“Formularios” de Ecatch es un módulo que permite la creación y configuración de formularios electrónicos de forma dinámica, ágil, y fácil, lo cual hace posible que una persona sin conocimiento en lenguajes de programación pueda crear un formulario para cualquier proceso."
  },
  {
    title: "Flujos de trabajo",
    description: "Workflow de Ecatch permite el modelamiento de procesos en notación BPMN 2.0 que    integra el  motor de reglas permitiendo que la parametrización de lógica de negocio de un proceso sea estructurada de manera ágil y fácil en una interfaz propia para ello."
  },
  {
    title: "Reportes",
    description: "Módulo de reportes Ecatch es una potente herramienta que permite a los usuarios crear informes dinámicos a partir de una fuente de datos obteniendo la información capturada y almacenada en los objetos de Ecatch. Ofrece una amplia variedad de opciones de visualización y personalización para comunicar de manera efectiva la información contenida en los datos."
  },
  {
    title: "Roles",
    description: "El módulo  de roles es una parte esencial de Ecatch, ya  que permite controlar la seguridad dentro de la plataforma para clasificar a  los usuarios que utilizaran la herramienta de tal manera que puedan realizar actividades de administración o gestión de los objetos dentro de Ecatch."
  },
  {
    title: "Objetos",
    description: "Esta utilidad permite la gestión de diferentes distintos tipos de contenedores de información u objetos que  serán los encargados de  transitar por los diferentes procesos de la herramienta, de esta manera se le dará gestión a la información, los objetos pueden ser representados con varios tipos; formularios, tipos documentales o documentos en distintas extensiones para distintos usos."
  },
  {
    title: "Entidades",
    description: "Es un configurador que se centra en estructura de datos adaptativas, implementación de algoritmos reactivos y la optimización para funcionar en un entorno en la nube. Se integra con formularios dinámicos, documentos y flujos de proceso."
  },
  {
    title: "Usuarios",
    description: "Es una parte esencial de nuestra plataforma que permite a los administradores y usuarios autorizados gestionar y personalizar perfiles de usuario."
  },
  {
    title: "Objetos",
    description: "Esta utilidad permite la gestión de diferentes distintos tipos de contenedores de información u objetos que  serán los encargados de  transitar por los diferentes procesos de la herramienta, de esta manera se le dará gestión a la información, los objetos pueden ser representados con varios tipos; formularios, tipos documentales o documentos en distintas extensiones para distintos usos."
  }
];

const ECatchStudioPage = () => {
  const [capacidadIndex, setCapacidadIndex] = useState(0);
  return (
  <Layout path="ecatchstudio">
     <SEO 
      title="ECatch Studio" 
      description="ECatch BPM es una suite de gestión de procesos empresariales 100% modular, Permite que la administración y la escalabilidad de procesos dentro de ella sean infinitos permitiendo crear muchos procesos con un único esquema de módulos."
      meta={[
        {
          name: `keywords`,
          content: `ECatch, ECatch Studio, ECatch Mobile, captura de información, automatización de procesos, reconocimiento BCR, OCR, ICR, OMR, ODR, simulación de procesos, formularios electrónicos, digitalización y captura, auto-documentación, monitoreo BPMN, colaborativo, validación de configuración, seguridad y auditoría`
        }
      ]}
    />
    <div className="w-full flex flex-col items-center">
      <div className="section-1 gap-12 flex flex-col-reverse md:flex-col lg:flex-row relative w-full h-fit lg:h-[560px] px-6 md:px-12 py-20" style={{maxWidth: "1440px"}}>
        <div className="z-20 flex flex-col items-center md:items-start gap-12">
          <div className="flex flex-col gap-6 items-center text-center md:text-left md:items-start">
            <p className="text-d" style={{fontSize: "40px", lineHeight: "56px"}}><span className="text-brand">ECatch Studio</span>, el portal <br className="hidden md:block"/>de configuración de<br className="hidden md:block"/>sus proyectos</p>
            <span className="text-subtle">ECatch Studio le ofrece una gran variedad de plantillas, componentes, <br className="hidden md:block"/>recursos y herramientas para que pueda dar rienda suelta a su creatividad y <br className="hidden md:block"/>desarrollar sus ideas con facilidad. ECatch Studio es el portal de <br className="hidden md:block"/>configuración de sus proyectos que necesita para llevar su negocio al <br className="hidden md:block"/>siguiente nivel.</span>
          </div>
          <a aria-label="Colombia (+57)" href="https://wa.link/se8g8h" target="_blank" className="b-default">
                    <div>
                        <svg className="fill-current text-icon-success" width="20" height="20" viewBox="0 0 20 20" fill="none" xmlns="http://www.w3.org/2000/svg">
                            <path fill-rule="evenodd" clip-rule="evenodd" d="M16.2004 3.79295C15.3914 2.9836 14.4304 2.34225 13.3726 1.90582C12.3147 1.46939 11.181 1.2465 10.0367 1.24998C5.23437 1.24998 1.325 5.13982 1.32305 9.92185C1.32092 11.4446 1.7223 12.9406 2.48633 14.2578L1.25 18.75L5.86914 17.5441C7.14774 18.2368 8.57903 18.5993 10.0332 18.5988H10.0367C14.8387 18.5988 18.7477 14.7086 18.75 9.92693C18.7529 8.78653 18.529 7.65693 18.0913 6.60386C17.6536 5.5508 17.0108 4.59529 16.2004 3.79295ZM10.0367 17.1351H10.0336C8.73726 17.1355 7.46449 16.7886 6.34766 16.1304L6.0832 15.9742L3.34219 16.6898L4.07383 14.0301L3.90156 13.7566C3.17671 12.609 2.79267 11.2792 2.79414 9.92185C2.79414 5.94803 6.04453 2.71482 10.0395 2.71482C11.9559 2.7114 13.7951 3.46933 15.1528 4.8219C16.5104 6.17448 17.2752 8.01092 17.2789 9.92732C17.2773 13.9015 14.0285 17.1351 10.0367 17.1351ZM14.009 11.7371C13.7914 11.6285 12.7199 11.1043 12.5215 11.032C12.323 10.9597 12.1766 10.9234 12.0316 11.1406C11.8867 11.3578 11.4691 11.8437 11.3422 11.9902C11.2152 12.1367 11.0883 12.1527 10.8707 12.0441C10.6531 11.9355 9.95117 11.707 9.11953 10.9687C8.47226 10.3941 8.03555 9.68475 7.90859 9.46795C7.78164 9.25115 7.89492 9.13357 8.0039 9.02576C8.10195 8.92849 8.22148 8.77264 8.33047 8.64607C8.43945 8.51951 8.47578 8.42889 8.54805 8.28435C8.62031 8.13982 8.58437 8.01326 8.53008 7.90506C8.47578 7.79685 8.04023 6.73006 7.85898 6.29607C7.68203 5.87342 7.50273 5.93084 7.36914 5.9242C7.24219 5.91795 7.0957 5.91639 6.95156 5.91639C6.84137 5.91925 6.73295 5.94481 6.63308 5.99145C6.53321 6.03809 6.44402 6.10483 6.37109 6.18748C6.17148 6.40467 5.60898 6.92967 5.60898 7.99529C5.60898 9.06092 6.39023 10.0922 6.49805 10.2367C6.60586 10.3812 8.0332 12.5699 10.2172 13.5086C10.6227 13.6822 11.0371 13.8345 11.4586 13.9648C11.9801 14.1297 12.4547 14.1066 12.8297 14.0508C13.248 13.9887 14.1187 13.5265 14.2996 13.0203C14.4805 12.514 14.4809 12.0805 14.4266 11.9902C14.3723 11.9 14.227 11.8453 14.009 11.7371Z"/>
                        </svg>
                    </div>
                    <span>Escríbenos para una demo</span>
                </a>
        </div>
        <div className="z-10 lg:absolute lg:top-20 lg:right-12">
          <StaticImage objectFit='contain' objectPosition="center" alt="portada" className="w-full lg:w-fit lg:h-[400px]" src="../images/ecapture/mockup-ecatch-config.png"/>
        </div>
      </div>
      <div className="section-2 w-full py-10 bg-selected-1 flex justify-center">
        <div className="flex flex-col gap-2 px-6 md:px-12 text-center items-center" style={{maxWidth: "1440px"}}>
          <span className="text-brand h-600" style={{fontStyle: "italic"}}>“Conoce más sobre ECatch Studio”</span>
          <div>
            <svg className="fill-current text-icon-brand" width="24" height="24" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg">
              <path fill-rule="evenodd" clip-rule="evenodd" d="M20.0303 4.71967C20.3232 5.01256 20.3232 5.48744 20.0303 5.78033L12.5303 13.2803C12.2374 13.5732 11.7626 13.5732 11.4697 13.2803L3.96967 5.78033C3.67678 5.48744 3.67678 5.01256 3.96967 4.71967C4.26256 4.42678 4.73744 4.42678 5.03033 4.71967L12 11.6893L18.9697 4.71967C19.2626 4.42678 19.7374 4.42678 20.0303 4.71967ZM20.0303 10.7197C20.3232 11.0126 20.3232 11.4874 20.0303 11.7803L12.5303 19.2803C12.2374 19.5732 11.7626 19.5732 11.4697 19.2803L3.96967 11.7803C3.67678 11.4874 3.67678 11.0126 3.96967 10.7197C4.26256 10.4268 4.73744 10.4268 5.03033 10.7197L12 17.6893L18.9697 10.7197C19.2626 10.4268 19.7374 10.4268 20.0303 10.7197Z"/>
            </svg>
          </div>
        </div>
      </div>

      <div className="section-3 w-full gap-12 flex flex-col items-center px-6 md:px-12 py-20" style={{maxWidth: "1440px"}}>
      <span className="text-center text-d h-900">Capacidades de ECatch Studio</span>
        <div className='w-full flex gap-10'>
            <div className='flex flex-col gap-5'>
            {capacidadesEcatch.map((capacidad, index) => (
                <button onClick={() => setCapacidadIndex(index)} key={index} className={index === capacidadIndex ? 'b-primary w-full':'b-default w-full'} style={{width: "160px"}}>{capacidad.title}</button>
            ))}
            </div>
            <div className='hidden md:block h-60 w-5 border-l border-t border-b border-d'></div>
            <div className='w-full flex flex-col gap-5'>
                {capacidadesEcatch.map((capacidad, index) => (
                    <div key={index} className={capacidadIndex === index?'w-full flex flex-col gap-8':'hidden'}>
                        <div className='flex flex-col gap-4'>
                            <span className='h-600 text-subtle'>{capacidad.title}</span>
                            <span className='text-subtle'>{capacidad.description}</span>
                        </div>
                        <StaticImage objectFit="contain" objectPosition="center" className="w-full" alt="mockup-ecatch-config" src="../images/ecapture/mockup-ecatch-config.png"/>
                    </div>
                ))}
            </div>
        </div>
      </div>


      <div className="section-3 w-full gap-12 flex flex-col items-center px-6 md:px-12 py-20" style={{maxWidth: "1440px"}}>
        <div className='flex items-center gap-2'>
          <span className="text-center text-d h-900">Modelador de procesos</span>
          <div className='w-fit p-1 rounded-lg bg-brand-1 text-inverse t-small'>BPMN 2.0</div>
        </div>
        <div>
        <StaticImage objectFit="contain" objectPosition="center" className="w-full" alt="mockup-ecatch-config" src="../images/ecapture/mockup-ecatch-workflow.png"/>
        <div className='h-12'></div>
        <span className='text-subtle'>Es un conjunto de componentes que permite tener todo un motor de reglas y acciones para parametrización de flujos de trabajo en cada una de las etapas del proceso. Puede asignar y controlar los tiempos de cada etapa del proceso, haciendo que se tengan controles en cada una y de ser requerido hacer cargas y de notificaciones a los usuarios que requieran ser notificados, con un potentee modelador BPMN 2.0 que facilita el entendimiento de los procesos con los usuarios funcionales.</span>
        <div className='pt-4 flex justify-center flex-wrap gap-2'>
            <div className='p-3 rounded border-d border-2 text-subtlest h-600'>
                Modelador
            </div>
            <div className='p-3 rounded border-d border-2 text-subtlest h-600'>
                Control de versiones
            </div>
            <div className='p-3 rounded border-d border-2 text-subtlest h-600'>
                Colas de proceso
            </div>
            <div className='p-3 rounded border-d border-2 text-subtlest h-600'>
                Balanceo de cargas
            </div>
            <div className='p-3 rounded border-d border-2 text-subtlest h-600'>
                Validaciones
            </div>
            <div className='p-3 rounded border-d border-2 text-subtlest h-600'>
                Simulación
            </div>
            <div className='p-3 rounded border-d border-2 text-subtlest h-600'>
                Auto-documentación
            </div>
            <div className='p-3 rounded border-d border-2 text-subtlest h-600'>
              Colaborativo
            </div>
        </div>
        </div>
      </div>



    </div>
  </Layout>
  );
}

export default ECatchStudioPage
